/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
  cursor: default;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: #fff;
}

#font-col {
  width: 37.5%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
}

#preview-col {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 37.5%;
  right: 0;
  bottom: 0;
  color: #000;
  text-align: center;
}

#font-col header,
#preview-col header {
  -webkit-app-region: drag;
  height: 40px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  font-size: 12px;
}

#preview-col header #show-fonts,
#preview-col header #show-glyphs {
  appearance: none;
  border: 0;
  width: 22px;
  height: 22px;
  margin: 0 5px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center top;
  outline: none;
  border-bottom: 2px solid #fff;
  transition: border-bottom-color .2s;
}

#preview-col header #show-fonts.selected,
#preview-col header #show-glyphs.selected {
  border-bottom-color: #34C0F9;
}

#preview-col header #show-fonts {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="22" height="17" viewBox="0 0 22 17"><g fill="rgba(0, 0, 0, 0.3)" fill-rule="evenodd"><rect width="22" height="2"/><rect width="18" height="2" x="2" y="5"/><rect width="22" height="2" y="10"/><rect width="16" height="2" x="3" y="15"/></g></svg>');
}

#preview-col header #show-glyphs {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="18" height="17" viewBox="0 0 18 17"><g fill="rgba(0, 0, 0, 0.3)" fill-rule="evenodd"><rect width="2" height="2"/><rect width="2" height="2" x="4"/><rect width="2" height="2" x="8"/><rect width="2" height="2" x="12"/><rect width="2" height="2" x="16"/><rect width="2" height="2" y="5"/><rect width="2" height="2" x="4" y="5"/><rect width="2" height="2" x="8" y="5"/><rect width="2" height="2" x="12" y="5"/><rect width="2" height="2" x="16" y="5"/><rect width="2" height="2" y="10"/><rect width="2" height="2" x="4" y="10"/><rect width="2" height="2" x="8" y="10"/><rect width="2" height="2" x="12" y="10"/><rect width="2" height="2" x="16" y="10"/><rect width="2" height="2" y="15"/><rect width="2" height="2" x="4" y="15"/><rect width="2" height="2" x="8" y="15"/><rect width="2" height="2" x="12" y="15"/><rect width="2" height="2" x="16" y="15"/></g></svg>\a');
}

#preview-col header #font-color-swatch {
  display: inline-block;
  margin: 8px 5px 0;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 3px solid #fff;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.3);
}

#preview-col header #font-color-swatch #font-color {
  visibility: hidden;
}

#preview-col header #glyph-types {
  position: absolute;
  top: 11px;
  right: 15px;
  display: none;
}

#preview-col footer {
  height: 40px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 12px;
  line-height: 40px;
}

#preview-col footer #font-size {
  width: 240px;
  vertical-align: middle;
  margin-right: 10px;
}

#preview-col footer .slider-label {
  display: inline-block;
  width: 40px;
}

main {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 40px;
  padding: 0 10%;
  overflow: auto;
  /* note(@duncanmid): scroll shadows by @kizmarh and @leaverou */
  background: linear-gradient(white 30%, rgba(255, 255, 255, 0)), linear-gradient(rgba(255, 255, 255, 0), white 70%) 0 100%, radial-gradient(farthest-side at 50% 0, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0)), radial-gradient(farthest-side at 50% 100%, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0)) 0 100%;
  background: linear-gradient(white 30%, rgba(255, 255, 255, 0)), linear-gradient(rgba(255, 255, 255, 0), white 70%) 0 100%, radial-gradient(farthest-side at 50% 0, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0)), radial-gradient(farthest-side at 50% 100%, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0)) 0 100%;
  background-repeat: no-repeat;
  background-size: 100% 40px, 100% 40px, 100% 14px, 100% 14px;
  background-attachment: local, local, scroll, scroll;
}

main::-webkit-scrollbar {
  width: 8px;
}

main::-webkit-scrollbar-thumb {
  background-color: rgba(3, 19, 32, 0.4);
  border-radius: 4px;
}

#font-list {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  padding: 0 12px 0 20px;
}

#font-list::-webkit-scrollbar {
  width: 8px;
}

#font-list::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 4px;
}

#font-list ul li {
  font-size: 15px;
  line-height: 26px;
  margin-bottom: 2px;
  position: relative;
  padding: 0 32px 0 40px;
  font-weight: 500;
  cursor: default;
  transition: background-color .15s ease-in-out;
  border-radius: 13px;
}

#font-list ul li::before {
  content: '';
  display: block;
  width: 18px;
  height: 16px;
  position: absolute;
  left: 10px;
  top: 5px;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="18" height="15" viewBox="0 0 18 15"><path fill="white" fill-rule="evenodd" d="M30.1999885,6 C31.1941074,6 32,6.96050137 32,8 L32,17.1195075 C32,18.1580748 31.1916089,19 30.1999885,19 L15.8000115,19 C14.8058926,19 14,18.1590061 14,17.1195075 L14,5.24975936 C14,4.55953633 14.5382361,4 15.2035152,4 L19.4029825,4 C20.6313955,4 21.6871935,4.89754675 22.1499946,6 L30.1999885,6 Z" transform="translate(-14 -4)"/></svg>') no-repeat center top;
  opacity: .5;
  transition: opacity .15s ease-in-out;
}

#font-list ul li.file {
  margin-left: 20px;
}

#font-list ul li.file::before {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="13" height="16" viewBox="0 0 13 16"><path fill="white" fill-rule="evenodd" d="M285,428 L294,428 C295.104569,428 296,428.895431 296,430 L296,442 C296,443.104569 295.104569,444 294,444 L285,444 C283.895431,444 283,443.104569 283,442 L283,430 C283,428.895431 283.895431,428 285,428 Z M291.849121,441 L293.969238,441 L290.681152,431.681641 L288.275391,431.681641 L285,441 L287.120117,441 L287.792969,438.791016 L291.17627,438.791016 L291.849121,441 Z M290.706543,437.140625 L288.288086,437.140625 C288.948245,435.00357 289.348144,433.664228 289.487793,433.122559 C289.521647,433.274903 289.577718,433.482258 289.656006,433.744629 C289.734294,434.007 290.08447,435.138987 290.706543,437.140625 Z" transform="translate(-283 -428)"/></svg>') no-repeat center top;
}

#font-list ul li::after {
  content: '';
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 3px;
  top: 3px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0;
  transition: opacity .15s ease-in-out;
}

#font-list ul li:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

#font-list ul li:hover::after {
  opacity: .2;
}

#font-list ul li div {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

#font-list ul li::after {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20"><path fill="rgba(255, 255, 255, 0.5)" fill-rule="evenodd" d="M249.070312,5.9296875 C251.023447,7.88282227 252,10.2395695 252,13 C252,15.7604305 251.023447,18.1171777 249.070312,20.0703125 C247.117178,22.0234473 244.76043,23 242,23 C239.23957,23 236.882822,22.0234473 234.929688,20.0703125 C232.976553,18.1171777 232,15.7604305 232,13 C232,10.2395695 232.976553,7.88282227 234.929688,5.9296875 C236.882822,3.97655273 239.23957,3 242,3 C244.76043,3 247.117178,3.97655273 249.070312,5.9296875 Z M244.687579,14.5036167 C244.961411,14.5235848 245.242261,14.4313545 245.455305,14.2256199 L245.934865,13.7625144 C246.332146,13.3788651 246.343196,12.7457966 245.959547,12.3485162 L241.791597,8.03247738 C241.407947,7.63519698 240.774879,7.62414673 240.377598,8.00779595 L239.898039,8.47090153 C239.500758,8.85455076 239.489708,9.4876193 239.873357,9.8848997 L242.651991,12.7622589 L239.534851,15.7724452 C239.137571,16.1560944 239.126521,16.7891629 239.51017,17.1864433 L239.973276,17.6660032 C240.356925,18.0632836 240.989993,18.0743339 241.387274,17.6906846 L244.687579,14.5036167 Z" transform="translate(-232 -3)"/></svg>');
}

#font-list ul li.selected {
  background-color: #34C0F9;
}

#font-list ul li.selected::before, #font-list ul li.selected::after {
  opacity: 1;
}

#preview-list,
#glyph-list {
  padding-top: 1.8em;
}

#glyph-list {
  display: none;
  text-align: center;
}

#glyph-list h2 {
  color: rgba(0, 0, 0, 0.3);
  font-weight: 600;
  padding-bottom: .8em;
}

#glyph-list .glyph {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #d8d8d8;
}

#glyph-list .glyph:before, #glyph-list .glyph:after {
  content: " ";
  display: table;
}

#glyph-list .glyph:after {
  clear: both;
}

#glyph-list .glyph figure {
  overflow: hidden;
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  border-radius: 16px;
  width: 128px;
  height: 128px;
  float: left;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color .25s, color .25s, border-color .25s;
}

#glyph-list .glyph figure:hover {
  background-color: #34C0F9;
  color: #fff !important;
  border-color: #07aff3;
}

#glyph-list .glyph table {
  line-height: 24px;
}

#glyph-list .glyph table th {
  text-align: right;
  font-size: 12px;
  text-transform: uppercase;
  padding-right: 15px;
}

#glyph-list .glyph table td {
  text-align: left;
  font-size: 14px;
  font-weight: 900;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  max-width: 0;
}

.preview-item {
  padding-bottom: 1.8em;
}

.preview-item h2 {
  color: rgba(0, 0, 0, 0.3);
  font-weight: 600;
  padding-bottom: .8em;
}

.preview-item div {
  word-break: break-all;
}

.glyph-item {
  padding: .5ch;
  margin: 4px;
  border-radius: .125ch;
  display: inline-block;
}
